<template>
	<view class="cinfo" v-if="commentator&&isCount&&userInfo">
		<view class="userInfo" v-for="(item,index) in commentator" :key="index" v-if="index < isCount">
			<view class="user">
				<image src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-ba46750a-9919-4a60-8473-439618d2c95d/83c12dbd-280b-41f7-b4fb-1cbddd4f13a8.png"></image>
				<view class="cont">
					<text>{{item.acid}}</text>
					<text style="font-size: 22rpx;">{{item.comContent}}</text>
					<view class="apply" @click.stop="handle(index)">
						回复
					</view>
				</view>
			</view>
			<template v-if="item.apply">
				<commentInfo isParent="false" :commentator="item.apply" :art_id="art_id" :isCount="isCount"></commentInfo>
			</template>
		</view>
		<uni-popup ref="popup" type="center">
			<uni-popup-dialog type="success"  mode="input" title="回复" @confirm="confirm"></uni-popup-dialog>
		</uni-popup>
	</view>
</template>

<script>
	export default {
		name: "CommentInfo",
		props: ["commentator","art_id","isParent","isCount"],
		data() {
			return {
				index:0,
				isP:null,
				userInfo:[]
			};
		},
		mounted() {
			this.getUserInfo();
			this.isP = this.isParent;
		},
		watch:{
		},
		methods: {
			getUserInfo() {
					for(var i=0;i<this.commentator.length;i++){
						uniCloud.callFunction({
							name:"getMineRes",
							data:{
								"acid":this.commentator[i].acid
							},
							success: (res) => {
								this.userInfo.push(res.result.data.data[0]);
							}
						})
					}
			},
			handle(i) {
				this.$refs.popup.open("center");
				this.index = i;
				// this.$forceUpdate();
			},
			confirm(val){
				let _this = this;
				while(_this.isP == "false"){
					_this = _this.$parent.$parent.$parent;
				}
				if(val){
					uni.getStorage({
						key:"token",
						success: (res) => {
							const obj = {
								"acid": res.data,
								"comContent": val,
								"apply": []
							};
							this.commentator[this.index].apply.push(obj);
							uniCloud.callFunction({
								name:"updateApplyComm",
								data:{
									"art_id":this.art_id,
									"commentator":_this.commentator
								},
								success:()=>{
								}
							})
						}
					})
				}
			}
		}
	}
</script>

<style scoped lang="scss">
	.cinfo {
		width: 100%;
		margin-top: 50rpx;

		.userInfo {
			width: 100%;
			padding: 10rpx 10rpx;
			box-sizing: border-box;
			margin-bottom: 20rpx;
			background-color: rgba(0, 123, 255, 0.1);

			.user {
				display: flex;

				>image {
					width: 100rpx;
					height: 100rpx;
					border-radius: 50%;
					margin-right: 20rpx;
				}

				>.cont {
					margin-top: 15rpx;
					display: flex;
					flex-direction: column;
					margin-top: 20rpx;
					justify-content: space-between;

				}
			}

		}
	}

	.apply {
		width: 48rpx;
		height: 32rpx;
		font-size: 24rpx;
		padding: 4rpx 16rpx;
		border-radius: 20rpx;
		border: 1px solid #ccc;
		color: $font-color;
		margin-top: 8rpx;
	}
</style>
